
@import 'assets/css/reset.css';
@import 'assets/css/typed.css';
/*样式设置*/
@import 'assets/css/fullpage.css';
.white-text {
  color: #fff;
}
.red-text {
  color: #f66;
}
.strong,
.bold {
  font-weight: bold;
}
/* 背景颜色 */
.red-section {
  background: #c0392b;
  color: #fff;
}
.green-section {
  background-color: #4fc08d;
  color: #fff;
}
.dark-section {
  background-color: #222;
  color: #fff;
}
.blue-section {
  background-color: #313640;
  color: #fff;
}
.split,
.light-section {
  background-color: #f5f5f5;
}
/* 文本对齐及样式 */
.text-center {
  text-align: center;
}
.text-left {
  text-align: left;
}
.thin-text {
  font-weight: 100;
}
/*水平对齐*/
.center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.right {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: end;
      -ms-flex-pack: end;
          justify-content: flex-end;
}
.left {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: start;
      -ms-flex-pack: start;
          justify-content: flex-start;
}
/*垂直对齐*/
.v-center {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
/* 图片页背景 */
.page,
.about {
  background-color: '#222';
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  padding: 0 10% 50px;
  text-align: center;
  position: relative;
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(63, 56, 56, 0.6)), to(rgba(63, 56, 56, 0.4))), url(assets/image/main_bg.jpg);
  background: linear-gradient(to bottom, rgba(63, 56, 56, 0.6), rgba(63, 56, 56, 0.4)), url(assets/image/main_bg.jpg);
  background-size: cover;
  background-position: center;
  color: #fff;
}
/* 导航样式 */
#nav {
  background: rgba(0, 0, 0, 0.8);
  border-radius: 2px;
  position: fixed;
  right: 2em;
  top: 30%;
  z-index: 100;
  text-align: center;
}
#nav li {
  line-height: 1;
  margin: 0 4px;
  padding: 10px 0;
  transition: all 0.3s ease-out;
  -webkit-transition: all 0.3s ease-out;
  width: 1.3em;
}
#nav li:hover {
  border-color: #3C6493;
}
#nav a {
  color: rgba(255, 255, 255, 0.5);
  text-decoration: none;
  font-size: 15px;
}
/*START: 提示信息：按键、计时器、页码 */
.tip-text {
  position: absolute;
  font-size: 13px;
  color: #fff;
}
.tip,
.page-no {
  position: absolute;
  font-size: 13px;
  color: #fff;
  right: 10px;
}
.tip-wrapper {
  padding: 3px;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 2px;
}
.tip {
  top: 10px;
}
.tip .key {
  padding: 3px;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 2px;
}
.clock {
  position: absolute;
  font-size: 13px;
  color: #fff;
  left: 10px;
}
.page-no,
.clock {
  bottom: 10px;
  padding: 3px;
  background-color: rgba(0, 0, 0, 0.8);
  border-radius: 2px;
}
/*END: 提示信息：按键、计时器、页码 */
/* slide 内页样式 */
.slide video {
  width: 100%;
  height: 100%;
  -o-object-fit: cover;
     object-fit: cover;
}
.wrap {
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  height: 100%;
  margin: 0;
  padding: 0;
  font-size: 20pt;
}
.wrap h4 {
  font-weight: 100;
  padding-bottom: 10px;
  font-size: 30pt;
}
.wrap .content {
  max-width: 90%;
  text-align: left;
}
.wrap .content p {
  margin: 20px 0;
}
.wrap a {
  color: #4fc08d;
  font-weight: 600;
}
.wrap strong {
  color: #4fc08d;
}
.wrap blockquote {
  position: relative;
  border-left: 4px solid #f66;
  background-color: #f8f8f8;
  border-bottom-right-radius: 2px;
  border-top-right-radius: 2px;
  color: #2c3e50;
  padding: .5em 1em;
  font-size: 16pt;
}
.wrap blockquote:before {
  content: '!';
  background-color: #f66;
  position: absolute;
  top: 14px;
  left: -12px;
  color: #fff;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  text-align: center;
  line-height: 20px;
  font-weight: bold;
  font-family: 'Dosis', 'Source Sans Pro', 'Helvetica Neue', Arial, sans-serif;
  font-size: 14px;
}
.wrap blockquote ul,
.wrap blockquote ol {
  padding-left: 1em;
}
.wrap blockquote ul li,
.wrap blockquote ol li {
  padding: 5px 0;
}
.wrap img {
  max-width: 100%;
  margin: 15px 0px;
  background: rgba(255, 255, 255, 0.12);
  border: 4px solid #fff;
  -webkit-box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
          box-shadow: 0 0 10px rgba(0, 0, 0, 0.15);
}
.wrap ol {
  margin-left: 1em;
}
.underline {
  text-decoration: none;
  -webkit-box-shadow: 0 1px;
          box-shadow: 0 1px;
  padding-bottom: 2px;
}
